// amplify/functions/rewriteResume/handler.js

function corsHeaders() {
    // Let Lambda URL CORS config handle Access-Control-Allow-Origin
    return {
        "Content-Type": "application/json",
        "Access-Control-Allow-Headers": "Content-Type",
        "Access-Control-Allow-Methods": "OPTIONS,POST",
    };
}

export const handler = async (event) => {
    try {
        if (event.requestContext?.http?.method === "OPTIONS") {
            return {
                statusCode: 204,
                headers: corsHeaders(),
                body: "",
            };
        }

        if (!event.body) {
            return {
                statusCode: 400,
                headers: corsHeaders(),
                body: JSON.stringify({ error: "Missing body" }),
            };
        }

        let parsed;
        try {
            parsed =
                typeof event.body === "string"
                    ? JSON.parse(event.body || "{}")
                    : event.body || {};
        } catch (e) {
            console.error("Invalid JSON body:", e);
            return {
                statusCode: 400,
                headers: corsHeaders(),
                body: JSON.stringify({ error: "Invalid JSON body" }),
            };
        }

        const resumeText = parsed.resumeText;
        const jobDescription = parsed.jobDescription;
        const language = parsed.language;
        const mode = parsed.mode || "standard";

        if (!resumeText || typeof resumeText !== "string") {
            return {
                statusCode: 400,
                headers: corsHeaders(),
                body: JSON.stringify({ error: "resumeText is required" }),
            };
        }

        const isSpanish = language === "es";
        const prefix = isSpanish
            ? mode === "federal"
                ? "VERSIÓN FEDERAL OPTIMIZADA (demo): "
                : "VERSIÓN OPTIMIZADA (demo): "
            : mode === "federal"
                ? "FEDERAL OPTIMIZED VERSION (demo): "
                : "OPTIMIZED VERSION (demo): ";

        const snippet =
            resumeText.length > 200
                ? resumeText.slice(0, 200) + "…"
                : resumeText;

        const rewrittenText = `${prefix}${snippet}`;

        return {
            statusCode: 200,
            headers: corsHeaders(),
            body: JSON.stringify({
                rewrittenText,
                jobDescription: jobDescription || null,
                mode,
            }),
        };
    } catch (err) {
        console.error("Rewrite Lambda error:", err);
        return {
            statusCode: 500,
            headers: corsHeaders(),
            body: JSON.stringify({
                error: "INTERNAL_ERROR",
                details: String(err && err.message ? err.message : err),
            }),
        };
    }
};
